
body {
  background: url(../src/bg-img.jpeg);
  background-position: center;
  background-size: cover;
  /* background: #801155; */
  color: #f7f7f7;
  font-size: 1rem;
  overflow-x: hidden;
  box-sizing: border-box;
}

.main-title {
  text-align: center;
  font-size: 220%;
  margin-top: 30px;
  font-weight: 400;
  letter-spacing: 2px;
}

.container {
  background: #2c364b;
  border-radius: 2rem;
  min-height: 90vh;
  width: 40%;
  margin-left: 30%;
}

.app-title {
  font-size: 120%;
  font-weight: 500;
  margin-left: 20px;
}

#todo-form input {
  padding: 14px;
  background: transparent;
  border: 1px solid #f5f5f5;
  border-radius: 10px;
  width: 62%;
  margin: 30px 40px;
  color: #f7f7f7;
}

#todo-form input::placeholder {
  color: #f7f7f7;
  font-weight: 200;
  font-size: 100%;
  font-family: inherit;
}

.add-btn{
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: .8s;
  padding: 14px 18px;
  background: #8d379e;
}

.add-btn:hover {
  background: #680a44;
}

#todo-form input:focus,
.add-btn:focus {
  outline: none;
}
