.list-items {
    width: 85%;
    background: #8d379e;
    height: 45px;
    margin: 20px auto;
    border-radius: 6px;
}

.list-items p {
    padding: 10px;
    position: relative;
}

.list-items span {
    position: absolute;
    right: 15px;
    cursor: pointer;
}

.delete-icon {
    margin: 0 5px;
}

.list-items input {
  background: transparent;
  border: none;
  color: #f7f7f7;
  font-size: 100%;
  outline: none;
}